.page-content {
  margin: 0 auto;
  max-width: 480px;
}

.page-content__text {
  color: #fff;
  max-width: 420px;
  line-height: 32px;
  font-size: 20px;
  margin: 20px 0;
}

.page-content__paragraph {
  margin: 0 0 12px 0;
}

.page-content__colored-text {
  color: #fff700;
}

@media screen and (max-width: 479px) {
  .page-content {
    margin: 0 12px;
  }

  .page-content__text {
    color: #fff;
    line-height: 28px;
    font-size: 16px;
    margin: 12px 0;
  }
}